/* Tree Card */
.card{
  position: relative;
  width: 320px;
  height: 420px;
  background-color: rgba(0, 0, 0, 0.609);
  border-radius: 20px;
  overflow: hidden;
}
.card::before{
  content: '';
  position: absolute;
  top: -50%;
  width: 100%;
  height: 100%;
  background-color: rgb(0, 89, 206);
  transform: skewY(345deg);
  transition: 0.5s;
}
.card:hover::before{
  transform: skewY(390deg);
  top: -70%;

}
.card .img{
  position: relative;
  width: 100%;
  display: flex;
  justify-content: center;
}
.textCard{
  color: white;
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin: 5% 0px;
}
.titleCard{
  margin: 30% 0%;
  font-size: 25px;
  font-family: cursive;
}
.displayCard{
  font-size: 12px;
  width: 80%;
  margin: 0% 8%;
  color: rgba(219, 213, 213, 0.657);
}
@media (max-width: 700px){
  .App-header {
    margin: 4% 0%;
