
.PerfilGeneral{
    margin: 2%;
    width: 45%;
}
.PerfilTitulo{
    margin: 2% 6%;
    text-align: left;
    font-size: 3rem;
    font-family: fantasy;
    color: rgb(10, 142, 182);
}
.PerfilDescripcion{
    color: white;
    font-family: inherit;
    text-align: left;
}
.PerfilDescripcionn{
    color: white;
    font-family: inherit;
    text-align: left;
}
.Perfilimg{
    margin: 5% 10%;
    width: 400px;
    border-radius: 1rem;
}
.PerfilimgTwo{
    display: none;
}
.Perfilimg{
    background: linear-gradient(rgba(0, 0, 0, 0.322),#080808e1), url(https://instagram.ftuc4-1.fna.fbcdn.net/v/t51.2885-15/e35/83335170_175960730414830_7604032542552467755_n.jpg?_nc_ht=instagram.ftuc4-1.fna.fbcdn.net&_nc_cat=110&_nc_ohc=e2yCvNBy340AX9HRRCD&tp=1&oh=24d0243362178981121f5ace2fcdff67&oe=6017BCC9);
    background-size: cover;
    background-position: center;
    height: 90vh;
}
.Perfilimg:hover{
    transition-duration: 2s, 2s;
    background: linear-gradient(rgba(0, 0, 0, 0.322),#00000063), url(https://instagram.ftuc4-1.fna.fbcdn.net/v/t51.2885-15/e35/83335170_175960730414830_7604032542552467755_n.jpg?_nc_ht=instagram.ftuc4-1.fna.fbcdn.net&_nc_cat=110&_nc_ohc=e2yCvNBy340AX9HRRCD&tp=1&oh=24d0243362178981121f5ace2fcdff67&oe=6017BCC9);
    background-size: cover;
    background-position: center;
    height: 90vh;
}
.PerfilDisplayImg{
    display: flex;
}
.carrucelDataTwo{
    display: none;
}
#TreeCarrucel{
    display: block;
}
@media (max-width: 700px){
    .PerfilDescripcionn{
        display: none;
        color: white;
        font-family: inherit;
        text-align: left;
    }
    .carrucelDataTwo{
        display: block;
    }
    #TreeCarrucel{
        display: none;
    }
    .PerfilimgTwo{
        display: block;
        background: linear-gradient(rgba(0, 0, 0, 0.322),#00000063), url(https://instagram.ftuc4-1.fna.fbcdn.net/v/t51.2885-15/e35/49616204_1184665255031219_7788414165813092270_n.jpg?_nc_ht=instagram.ftuc4-1.fna.fbcdn.net&_nc_cat=111&_nc_ohc=e3-MGONsiwAAX9mhcpG&tp=1&oh=b2fc0ba90b1736b4310e9e034f96912c&oe=6017E6C6);
        background-size: cover;
        background-position: center;
        width: 345px;
        height: 245px;
        border-radius: 1rem;
    }
    .PerfilTitulo{
        margin: 3% 0%;
        text-align: center;
        font-size: 2rem;
        font-family: fantasy;
    }
    .PerfilDescripcion{
        color: white;
        font-family: inherit;
        text-align: left;
    }
    .PerfilGeneral{
        margin: 2%;
        width: 100%;
    }
}

